<template>
  <Row style="margin-top: 30px">
    <Row>
    <Col span="4" offset="4">
      总共{{total}}篇相关文章
    </Col>
    </Row>
    <Row v-for="item in textsList" :key="item.textId" >
      <Col offset="6">
        <Row type="flex" align="middle">
          <Col offset="1" style="font-size: 15px;">
            <Button @click="goUser(item)" type="text">
              <img class="round_icon" src="../../../static/img/default.jpg"
                   v-if="item.user.userHeadportrait==='default.jpg'"/>
              <Head-portrait :src="item.user.userHeadportrait" v-else></Head-portrait>
            </Button>
          </Col>
          <Col class="headBox">
            <p style="font-size:25px;">
              {{item.textTitle}}
            </p>
            <span  >
              <Tooltip content="作者" style="margin-right: 5px">
              <h5>{{item.user.userName}}</h5>
              </Tooltip>
            </span>
            <Tooltip content="最后修改日期" style="margin-right: 5px">
              {{item.textReleasedate}}
            </Tooltip>
            <span>点击量 {{item.textCount}}</span>
            <span>评论数 {{item.textCommentnum}}</span>
            <span>点赞数 {{item.textLikenum}}</span>
            <span>收藏数 {{item.textCollectnum}}</span>
          </Col>
        </Row>
        <Row>
          <Col offset="2">
            <p v-html="item.textContent.replace(/<(style|script|iframe)[^>]*?>[\s\S]+?<\/\1\s*>/gi, '').
          replace(/<[^>]+?>/g, '').replace(/\s+/g, ' ').replace(/ /g, ' ').replace(/>/g, ' ').substring(0,300)"
               style="width: 700px">
            </p>
          </Col>
          <Col offset="12" >
            <Button type="text" @click="goText(item)">查看更多内容</Button>
          </Col>
          <Col  style="border-bottom: 1px solid gray;width: 780px;margin-left: 50px">
          </Col>
        </Row>
      </Col>
    </Row>
  <Row type="flex" justify="center" align="middle">
    <Col style="font-weight:bolder;font-size: 25px">
      到底部了QAQ
    </Col>
  </Row>
  <Row type="flex" justify="center">
    <Col>
      <Page :total="total" :current="pageNum" show-elevator @on-change="pageChange"/>
    </Col>
  </Row>
  </Row>
</template>

<script>
  import HeadPortrait from '../page/HeadPortrait'

  export default {
        name: "Search",
        data() {
            return {
              title:'',
              textsList:[],
              pages: 0,
              pageSize: 10,
              pageNum: 1,
              focusList: [],
              total: 0,
            }
        }, methods: {
          getByTitle(){
            this.title=this.$route.params.title
            this.axios.get('textByTitle',{params:{title:this.title,pageNum:this.pageNum}}).then(res=>{
              if(res.data.code===100){
                this.textsList=res.data.map.texts.list
                this.pages = res.data.map.texts.pages
                this.pageNum = res.data.map.texts.pageNum
                this.pageSize = res.data.map.texts.pageSize
                this.total = res.data.map.texts.total
              }else{
                this.$Message.error("没有找到该文章")
              }
            }).catch(error=>{
              this.$Message.warning("查找文章失败")
            })
          }, pageChange(page) {
          this.pageNum = page
        },goText(item){
        this.$router.push('/text?textId='+item.textId)
      },goUser(item){
        this.$router.push('/personHome/'+item.textWriterid)
      }, pageChange(page) {
        this.pageNum = page
      }
      }, components: {HeadPortrait}, mounted() {
            this.getByTitle()
      },watch:{
      pageNum:function () {
        this.getByTitle()
      }
    }
    }
</script>

<style scoped>
  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }
</style>



